  }
  
  .view-options {
      float: right;
  }
  
  .edit-field {
      margin-top: -10px;
  }
  
  .y-padding {
      padding: 0px 20px;
  }
  
  .justify-text {
      text-align: justify;
  }
  
  .emp-top {
      margin-top: 10px;
      border-radius: 20px;
  }
  
  #register-user {
      padding-top: 20px;
  }
  
  .small-dialog {
      text-align: -webkit-center;
      padding-top: 50px;
  }
  
  .dialog-contents {
      width: 500px;
      text-align: center;
  }
  
  .no-margin {
      margin: 0px !important;
  }
  
  .about-link {
      float: right;
      padding-right: 20px;
  }
  
  .form-field {
      margin-top: -20px;
  }
  
  .padding-fields {
      padding-left: 10px;
      padding-right: 10px;
  }
  
  .login-link {
      float: right;
  }
  
  /* Scrollbar */
  
  .logout-btn {
      margin-top: 21px;
      background-color: #410e15;
      border: none;
      color: white;
  }
  
  .logout-btn:hover {
      margin-top: 21px;
      background-color: #410e15;
      border: none;
      color: white;
      cursor: pointer;
  }
  
  .logout-btn:focus {
      margin-top: 21px;
      background-color: #410e15;
      border: none;
      color: white;
      cursor: pointer;
  }
  
  ::-webkit-scrollbar {
      background-color: #eaeaec;
      border-radius: 10px;
  }
  
  ::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 5px grey;
              box-shadow: inset 0 0 5px grey;
      border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb {
      background: #410e15;
      border-radius: 10px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
      background: #410e15;
  }